import React, { Suspense, lazy } from 'react';
import { Routes, Route } from 'react-router-dom';
import './App.css';

// 1. Import Layout
import MainLayout from './components/layout/MainLayout';

// 2. --- Lazy Load ALL pages here ---
const Home = lazy(() => import('./pages/lazy/Home'));
const About = lazy(() => import('./pages/lazy/About'));
const NotFound = lazy(() => import('./pages/lazy/NotFound'));
const Customers = lazy(() => import('./pages/Customers'));
const Products = lazy(() => import('./pages/Products'));
const Login = lazy(() => import('./pages/Login')); 

function App() {
  
  // 3. ---  Login ( 33) ---
  const auth_user_id = localStorage.getItem("auth_user_id");

  return (
    <Suspense fallback={<div style={{ color: 'white' }}>Loading...</div>}>
      <Routes>
        {/* 4. ---  auth_user_id  --- */}
        {auth_user_id != null ? (
          /* --- 5. ( Login )  Navbar --- */
          <>
            <Route path="/home" element={<MainLayoutWrapper><Home /></MainLayoutWrapper>} />
            <Route path="/customers" element={<MainLayoutWrapper><Customers /></MainLayoutWrapper>} />
            <Route path="/products/:type" element={<MainLayoutWrapper><Products /></MainLayoutWrapper>} />
            <Route path="/about" element={<MainLayoutWrapper><About /></MainLayoutWrapper>} />
            
            {/* ( /  /login  home ) */}
            <Route path="/" element={<MainLayoutWrapper><Home /></MainLayoutWrapper>} />
            <Route path="/login" element={<MainLayoutWrapper><Home /></MainLayoutWrapper>} />
            <Route path="*" element={<NotFound />} />
          </>
        ) : (
          /* --- 6. ( Login)  Login --- */
          <>
            <Route path="/" element={<Login />} />
            <Route path="/login" element={<Login />} />
            <Route path="*" element={<Login />} /> 
          </>
        )}
      </Routes>
    </Suspense>
  );
}

// 7. --- Helper component ( Layout) ---
const MainLayoutWrapper = ({ children }) => (
  <MainLayout>
    {children}
  </MainLayout>
);

export default App;