import React, { useState, useEffect } from 'react';
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap"; 
import { Link } from "react-router-dom";
import axios from 'axios'; 

// --- 1. API Config ---
const AUTH_TABLE_ID = "733326"; 
const API_TOKEN = "pJmbVWCV1NFQ7333Cmqj822hINsyguMW";

function NavbarComponent() {

  // --- 2. State  ID ---
  const auth_user_id = localStorage.getItem("auth_user_id"); 
  const [firstName, setFirstName] = useState(""); 
  const [lastName, setLastName] = useState(""); 

  // --- 3.  User ---
  function getUser() { 
    if (!auth_user_id) return; 

    let config = {
      method: "get",
      url: `https://api.baserow.io/api/database/rows/table/${AUTH_TABLE_ID}/${auth_user_id}/?user_field_names=true`,
      headers: {
        Authorization: `Token ${API_TOKEN}`,
      },
    };

    axios
      .request(config)
      .then((response) => {
        setFirstName(response.data?.first_name || "User"); 
        setLastName(response.data?.last_name || ""); 
      })
      .catch((error) => {
        console.log(error);
        handleLogout();
      });
  }

  // --- 4.  Logout () ---
  const handleLogout = () => { 
    localStorage.clear(); 
    //  :  path  GitHub Pages + HashRouter
    window.location.href = "/flow-project/#/login"; 
  };

  // --- 5.  getUser  ---
  useEffect(() => { 
    getUser();
  }, []); 


  return (
    <Navbar data-bs-theme="dark" className="navbar-tech-glow">
      <Container>
        {/*  :  as={Link}  href  Reload */}
        <Navbar.Brand as={Link} to="/home">FLOW | </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/home">
              
            </Nav.Link>
            <Nav.Link as={Link} to="/customers">
              
            </Nav.Link>
            <NavDropdown title="" id="basic-nav-dropdown">
              {/*  :  as={Link}  href */}
              <NavDropdown.Item as={Link} to="/products/">
                
              </NavDropdown.Item>
              <NavDropdown.Item as={Link} to="/products/">
                
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link as={Link} to="/about">
              
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>

        {/* --- 6. Dropdown  --- */}
        <Navbar.Collapse className="justify-content-end">
          <Nav>
            <NavDropdown 
              title={`${firstName} ${lastName}`} 
              id="basic-nav-dropdown-user"
              align="end" 
            >
              <NavDropdown.Header></NavDropdown.Header>
              <NavDropdown.Item disabled>
                [  00001 ] 
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item onClick={handleLogout} style={{ color: '#dc3545' }}>
                
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>

      </Container>
    </Navbar>
  );
}
export default NavbarComponent;